import React from 'react';
import { findDOMNode } from 'react-dom';
import $ from 'jquery';
import { TabContent, TabPane, Nav, NavItem, NavLink, Row, Col, Button } from 'reactstrap';
import classnames from 'classnames';


import '../styles/Primary-button.css';

class Login_Register_Tabs extends React.Component {
    constructor(props) {
        super(props);

        this.toggle = this.toggle.bind(this);
        this.state = {
            activeTab: '1'
        };
    }

    toggle(tab) {
        if (this.state.activeTab !== tab) {
            this.setState({
                activeTab: tab
            });
        }
    }

    render() {
        return (
            <div>
                <Nav tabs>
                    <NavItem>
                        <NavLink
                            className={classnames({ active: this.state.activeTab === '1' })}
                            onClick={() => { this.toggle('1'); }}>
                            Register
                        </NavLink>
                    </NavItem>
                    <NavItem>
                        <NavLink
                            className={classnames({ active: this.state.activeTab === '2' })}
                            onClick={() => { this.toggle('2'); }}>
                            Login
                        </NavLink>
                    </NavItem>
                </Nav>
                <TabContent activeTab={this.state.activeTab}>
                    <TabPane tabId='1'>
                        <h2 className='get-started-title text-center'>Hi <span>there!</span></h2>
                        <Row>
                            <Col sm='12'>
                                <form className='authorisation-form d-flex flex-column align-items-center' method='post'>
                                    <ul className=''>
                                        <li className='username'><input type='text' placeholder='Username'/></li>
                                        <li className='password'><input type='text' placeholder='Password'/></li>
                                        <li className='password'><input type='text' placeholder='Repeat your password'/></li>
                                        <li className='email'><input type='text' placeholder='E-mail'/></li>
                                    </ul>
                                    <Button>Enter</Button>
                                </form>
                            </Col>
                        </Row>
                    </TabPane>
                    <TabPane tabId='2'>
                        <h2 className='get-started-title text-center'>Welcome <span>back!</span></h2>
                        <Row>
                            <Col sm='12'>
                                <form className='authorisation-form d-flex flex-column align-items-center'>
                                    <ul className=''>
                                        <li className='username'><input type='text' placeholder='Username'/></li>
                                        <li className='password'><input type='text' placeholder='Password'/></li>
                                    </ul>
                                    <Button>Enter</Button>
                                </form>
                            </Col>
                        </Row>
                    </TabPane>
                </TabContent>
            </div>
        );
    }
}

export default Login_Register_Tabs;




